import styled from '@emotion/styled/macro'
export const Aside = styled.aside`
  background-color: #1035ad;
  font-weight: 400;
  text-transform: uppercase;
  width: 300px;
  padding: 15px;
`
export const Block = styled.div`
  padding: 15px;
  
  border: 3px solid #fff;
  border-radius: 10px;
`
export const Item = styled.li`
  padding: 15px;
  font-weight: 500;
  color: #71b609;
 
`
